<template>
  <v-container
    fluid
    tag="section"
  >
    <v-card
      class="px-5 py-3"
    >
      <v-simple-table
        fixed-header
        height="300px"
      >
        <template v-slot:default>
          <thead>
            <tr>
              <th class="table-title">Название источника</th>
              <th class="table-title">Город</th>
              <th class="table-title">Дата начало работ</th>
              <th class="table-title">Размер комиссии</th>
              <th class="table-title">Блоки</th>
              <th class="table-title">Доступ</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="item in $store.state.salesChannel"
              :key="item.name"
            >
              <td>{{ item.name }}</td>
              <td>{{ item.city }}</td>
              <td>{{ item.datebeg }}</td>
              <td>{{ item.commSize }}</td>
              <td>{{ item.blocks }}</td>
              <td>
                <v-btn
                  text
                  small
                  :color="item.perm?'primary':'error'"
                  @click="$store.commit('OPEN_CLOSE_SALES_CHANNEL', item.id - 1)"
                >
                  <span
                    v-if="item.perm"
                  >Открыть</span>
                  <span v-else>
                    Закрыть
                  </span>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <v-row>
        <v-col>
          <v-btn color="primary">Создать</v-btn>
          <v-btn color="primary">Настройки</v-btn>
          <v-btn color="primary">Добавить блоки</v-btn>
          <v-btn color="primary">Выход</v-btn>
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
  export default {
    name: 'SalesChannels',
    data () {
      return {
        headers: [
          {
            text: 'Название источника',
            align: 'start',
            sortable: false,
            value: 'name',
            class: 'table-title',
          },
          { text: 'Город', value: 'city', class: 'table-title' },
          { text: 'Дата начало работ', value: 'dateBeg', class: 'table-title' },
          { text: 'Размер комиссии', value: 'commSize', class: 'table-title' },
          { text: 'Блоки', value: 'blocks', class: 'table-title' },
          { text: 'Доступ', value: 'perm', class: 'table-title' },
        ],
      }
    },
  }
</script>

<style>
  .table-title {
    font-size: 14px !important;
    font-weight: 500 !important;
    color: orange !important;
  }
</style>
